import React, { useState, useEffect } from "react";
import { Layout } from "antd";
import NavBar from '../../components/navbar';
import SideBar from '../../components/sidebar';
import { Emitter } from "../../utils/emitter";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import { MenuItens } from "./itens";
import { Menu } from "antd";
import { Trans } from "react-i18next";


function Main() {
    const navigate = useNavigate();
    const location = useLocation();
    const [selectedKey, setSelectedKey] = useState(location.pathname);
    const [title, setTitle] = useState('');

    const handleClick = (event: any) => {
        console.log(event)
        Emitter.EventEmitter.emit(Emitter.Event.Action.MenuClicked, true);
        setSelectedKey(event.key);
        navigate(event.key);
    };
    
    useEffect(() => {
        Emitter.EventEmitter.addListener(Emitter.Event.Action.SetHeaderTitle, ((data: any) => {
            console.log({ data })
            setTitle(data)
        }));
    })


    // [antd: Menu] `children` will be removed in next major version. Please use `items` instead.
    const MenuCompomnent = <Menu mode="inline" onClick={handleClick} selectedKeys={[selectedKey]} theme="dark">
        {
            MenuItens.map((item, index) => <Menu.Item key={item.key}>
                {item.label}
            </Menu.Item>)
        }
    </Menu>

    return (
        <div className="App">
            <NavBar menu={MenuCompomnent} />
            <Layout>
                <SideBar menu={MenuCompomnent} />
                <Layout.Content className="content content-responsive bg-primary text-white">
                    <h1>{title}</h1>
                    <Outlet />
                </Layout.Content>
            </Layout>
        </div>
    );

}

export default Main;